$primary-color: #0f0f11;
$white: #ffffff;
@mixin onDesktop {
  @media (min-width: 1440px) {
    @content;
  }
}
@mixin onPortable {
  @media (min-width: 640px) {
    @content;
  }
}

.title {
  margin-top: 24px;
  color: $primary-color;
  margin-bottom: 16px;
  width: 100%;

  @include onPortable {
    margin-top: 16px;
  }
}

.cart__page {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 24px;
  margin-bottom: 64px;

  @include onDesktop {
    gap: 16px;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
  }
}